/* Reset margin and padding for the entire body to remove white space */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Fullscreen background gradient */
.App {
  font-family: Arial, sans-serif;
  color: #333;
  min-height: 100vh;
  background: linear-gradient(135deg, #f0f0f0, #d0e1ff);
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Profile Section */
.App-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.profile-text h1 {
  font-size: 18px;
  color: #888;
  margin: 0;
}

.profile-text h2 {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

/* About Statement */
.about-statement {
  margin: 40px 0;
  text-align: center;
}

.about-statement h3 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

/* Projects Section */
.projects-section {
  width: 100%;
  padding: 20px;
  max-width: 1000px;
}

.projects-section h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
  text-align: center;
}

/* Remove hyperlink underline and set white background for project cards */
.project-link {
  text-decoration: none; /* Remove underline */
  color: inherit;
}

.project-card {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
  background-color: #fff; /* White background */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 150px;
  height: auto;
  margin-right: 20px;
  border-radius: 8px;
}

.project-info h3 {
  font-size: 22px;
  margin: 0;
}

.project-info p {
  font-size: 14px;
  color: #666;
}
